import classNames from "classnames";
import { useEffect } from "react";
import { useState } from "react";

import "react-clock/dist/Clock.css";

function TimeInput({ primary, secondary, onChange, getValue }) {
  const currentDate = getValue();
  const [minutes, setMinutes] = useState(new Date(currentDate).getMinutes());
  const [hours, setHours] = useState(new Date(currentDate).getHours());
  useEffect(() => {
    const dt = new Date();
    dt.setMinutes(8);
    let val = dt.getMinutes().toString();
    if (val.length === 1) {
      val = "0" + val;
    }

    setMinutes(val);
  }, []);

  const onMinutesChange = (event) => {
    let val = event.target.value;
    if (val.length === 1) {
      val = 0 + val;
    }
    if (val < 0) {
      val = "59";
    }

    if (val.length > 2) {
      val = val.slice(-2);
    }

    if (event.target.value > 59) {
      val = "00";
    }

    setMinutes(val);

    const dt = new Date();
    dt.setMinutes(val);
    console.log(dt);
    onChange(dt);
  };

  const onHoursChange = (event) => {
    console.log(event.target.value);
    let val = event.target.value;
    if (val.length === 1) {
      val = 0 + val;
    }
    if (val < 0) {
      val = "23";
    }

    if (val.length > 2) {
      val = val.slice(-2);
    }

    if (event.target.value > 23) {
      val = "00";
    }

    console.log(val);
    setHours(val);

    const dt = new Date();
    dt.setHours(val);
    console.log(dt);
    onChange(dt);
  };

  const classText = classNames("time__text", {
    "time__text--secondary": secondary,
    "time__text--primary": primary,
  });

  return (
    <div className="time">
      <div className={classText}>
        <input
          className="time__input time__input--hours"
          type="number"
          value={hours}
          onChange={onHoursChange}
        />{" "}
        :
        <input
          className="time__input time__input--minutes"
          type="number"
          value={minutes}
          onChange={onMinutesChange}
        />
      </div>
    </div>
  );
}
export default TimeInput;
