import Activity from "../ui/Activity";
import SuggListItem from "./SuggListItem";

function SuggList({ suggData, isLoading, setEntry }) {
  let content;
//   if(!suggData){
//       return (
//           <div className="suggs">
//                <div className="suggs__error">An error occured</div>
//           </div>
       
//       )
//   }
  if (suggData) {
    content = suggData.map((place) => (
      <SuggListItem key={place.id} place={place} setEntry={setEntry} />
    ));
  } else{
    content = []
      
  }
  return (
    <div className="suggs">
      {isLoading ? (
        <Activity />
      ) : content.length === 0 ? (
        <div className="suggs__error">No results found</div>
      ) : (
        content
      )}
    </div>
  );
}
export default SuggList;
